import React, { useState } from 'react'
import { RequestData } from '../interfaces';
import axios from 'axios';

type Props = {
  handleFormSubmit: (data:any) => void,
}

const InsertForm = ({handleFormSubmit}: Props) => {

  const initialRequestData: RequestData = {
    hostname: '10.0.10.107',
    username: 'pe280\\jporras',
    password: 'Z@bb1.2022'
  };

  const [requestData, setRequestData] = useState<RequestData>(initialRequestData);

  const handleInputChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = event.target;
    setRequestData(prevState => ({
      ...prevState,
      [name]: value
    }));
  };
  
  const handleSelectChange = (event: React.ChangeEvent<HTMLSelectElement>) => {
    const { name, value } = event.target;
    setRequestData(prevState => ({
      ...prevState,
      [name]: value
    }));
  };

  const handleSubmit = (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    axios.post('http://10.0.10.180:8989/api/monitoreo/getvmwarereport', requestData)
      .then(response => {
        console.log(response.data);
        handleFormSubmit(response.data);
      })
      .catch(error => {
        console.log(error);
      });
  };

  return (
    <form onSubmit={handleSubmit} className="flex flex-col items-left space-y-2">
      <label htmlFor="hostname" className="text-lg font-semibold">Hostname:</label>
      <select name="hostname" id="hostname" onChange={handleSelectChange} value={requestData.hostname} className="w-full px-2 py-1 border rounded-md">
        <option value="172.16.6.108">172.16.6.108</option>
        <option value="10.0.10.106">10.0.10.106</option>
        <option value="10.0.10.107">10.0.10.107</option>
        <option value="10.0.12.91">10.0.12.91</option>
        <option value="10.0.12.92">10.0.12.92</option>
      </select>
      <label htmlFor="username" className="text-lg font-semibold">Username:</label>
      <input type="text" name="username" id="username" onChange={handleInputChange} value={requestData.username} className="w-full px-2 py-1 border rounded-md" />
      <label htmlFor="password" className="text-lg font-semibold">Password:</label>
      <input type="password" name="password" id="password" onChange={handleInputChange} value={requestData.password} className="w-full px-2 py-1 border rounded-md" />
      <button type="submit" className="bg-blue-500 text-white px-4 py-2 rounded-md">Submit</button>
    </form>
  );
}

export default InsertForm